import { NJSCORE } from '../../../lib'


const getDiv = (text, event) => {
    const div = document.createElement('div')
    const txtDiv = document.createElement('span')
    txtDiv.textContent = text
    const myInput = document.createElement('input')
    myInput.oninput = (e) => event(e)

    div.append(txtDiv)
    div.append(myInput)
    return div
}


export class Solution {
    public divHost: HTMLElement;
    public out: HTMLElement;
    public outHis: HTMLElement;
    constructor(id: string) {
        this.divHost = document.getElementById(id)


        const container = document.createElement('div');
        container.style.display ='flex'
        this.divHost.append(container)

        const inA = getDiv('A * B + C === Target: ',(e) => {
            const val = +e.target.value;
            this.compute(val)
        });
        container.append(inA)
        this.out = document.createElement('div');
        this.out.style.marginTop = '10px'
        this.divHost.append(this.out)
        this.outHis = document.createElement('div');
        this.outHis.style.marginTop = '10px'
        this.divHost.append(this.outHis)


        

    }
    public compute(target) {

        const compute = (param: NJSCORE.Param) => {
            return param.param[0] * param.param[1] + param.param[2] // + param.param[3];
        };
        const costFunction = (param: NJSCORE.Param, learningRate =  0.02) => {
            let result =  compute(param);
            // result = result - 100;
            // return Math.pow(1 - learningRate, Math.abs(result));
            result = result - target;
            return Math.pow(1 - learningRate, Math.abs(result));
        };
        
        const ga = new NJSCORE.GANumber(3);
        const result = ga.evolution(costFunction);

        const A = ga.history;
        const B = ga.historyScore;

        // console.log(A)
        // console.log(B)
        // console.log(result)
        this.outHis.textContent = JSON.stringify(A);
        this.out.textContent = JSON.stringify(result);

    }
    public destroy() {
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

